    import {  
        mapState,  
        mapMutations  
    } from 'vuex';  
    export default {  
		data() {
			return {
				storeInfo:[],
				pinfo:[],
				urldata:''
			}				
		},
        computed: mapState(['sellerHasLogin']),  
		onLoad(e) {
			this.urldata = e.udata;
			 console.log('页面加载');
			this.loadData();
		},
		onShow() {
             console.log('页面显示..');
         },
        methods: {   
            async loadData() { 
				let _this = this;
				console.log('===sellerHasLogin==='+this.sellerHasLogin);
				
                if (!this.sellerHasLogin) {  
					uni.navigateTo({
						url:'login',
					})
                }else{
					this.$Request.post('seller_pointorder/order_info',{str:this.urldata}).then(res => {
						if (res.code == 200) {
							console.log(res);
							_this.pinfo = res.datas.prod_info;
							
						}
						else{
							uni.showToast({
								icon: 'none',
								title:res.datas.error,
								duration:2000,
								success: function () {
									 setTimeout(function () {
										uni.redirectTo({
											url:'pointorder'
										});
									 }, 1500);
								}
							});
							
						}
					});
				}
            },
			toPage: function(url) {
				uni.navigateTo({
					url:url,
				})
			},
			toScan(){
				let _this = this;
				 uni.showLoading({title: '正在处理中...'});
				 let info = _this.urldata;
				 //let info = info.replace('POINTS:','');
				_this.$Request.post('seller_pointorder/point_scan',{str:info}).then(rest => {
					if (rest.code == 200) {
						uni.hideLoading();
						console.log(rest);
						uni.showToast({
							icon: 'none',
							title:'兑换成功！',
							duration:2000,
							success: function () {
								 setTimeout(function () {
									uni.redirectTo({
										url:'pointorder', 
										success (res) { 
											let page = getCurrentPages().pop(); 
											if (page == undefined || page == null) { 
											  return ;
											 } 
											 page.onLoad() ;
										}
									});
								 }, 1500);
							}
						});
						
					}
					else{
						uni.hideLoading();
						uni.showToast({
							icon: 'none',
							title:rest.datas.error,
							duration:2000
						});
					}
				});
			
			}
			 
        }  
    }  
